.navbar {

	&-nav {

		&:last-child {

			.nav-item {

				&:last-child {
					border: 0;
				}
			}
		}

		.nav-item {
			display: block;
			border-bottom: 1px solid #EEEEEE;
			padding: 1.125rem 0;

			@media (min-width: 1400px) {
				border: 0;
				padding: 0;
			}
		}

		.nav-link {
			--_fs: 1.25rem;
			--_color: #6F6F6F;
			--_padding: 0 .375rem;

			--_color-hover: var(--light);
			--_color-active: var(--light);

			--_bg-hover: var(--primary);
			--_bg-active: var(--primary);

			margin: 0 -.375rem;

			@media (min-width: 1400px) {
				--_fs: .875rem;

				margin: 0;
			}
		}
	}
}